import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import * as $ from 'jquery';
import 'slick-carousel';
//import { ActivatedRoute, Router } from '@angular/router';
//import { UserService } from './../../../../services/user.service';
import { Title } from '@angular/platform-browser';
import { JobService } from '../../../services/job.service';
//import { PasswordValidator } from '../../../validator/password.validator';
import { FlashMessagesService } from 'angular2-flash-messages';


@Component({
  selector: 'app-home',
  templateUrl: 'index.component.html',
   styleUrls : [ './index-component.css' ]
})
export class IndexComponent implements OnInit {
	title = 'Home Page';
	allplans:any="";jobservices:any="";
	constructor(private _flashMessagesService: FlashMessagesService,private jobservice: JobService,private titleService: Title) { 

		this.getAllplans();this.getServices();
		this.titleService.setTitle( 'Home | MoveLaunch' );
	}
	
	getAllplans(){
		this.jobservice.getAllPlans().subscribe(result =>
		{
			
			if(result.success){
				
				this.allplans = result.data;	
				
			}
		   
		}); 
	} 
	setuserpartner(usertype){
		localStorage.setItem('usertype',usertype);
	}
	getServices(){
		this.jobservice.getServices().subscribe(result => {
			console.log(result);
			this.jobservices =  result.data;
		
		}); 
	}

	isLoggedIn() {
	   let token = localStorage.getItem('login_token');
		if(token){
			return true ;
		}else{
			return false ;
		}
		
	}
  ngOnInit() {
		
	     $(document).ready(function(){
			(<any>$('#reviews')).slick({
					  slidesToShow: 1,
					  slidesToScroll: 1,
					  arrows: false,
					  autoplay: false,
					  slide: 'div',
					  fade: false,
					  infinite: true,
					  dots: true
			 });
		 
		 (<any>$('#companies-carousel')).slick({
				  slidesToShow: 4,
				  slidesToScroll: 1,
				  arrows: true,
				  autoplay: true,
				  slide: 'div',
				  fade: false,
				  infinite: true,
				  dots: false,
				  responsive: [
					{
					  breakpoint: 980,
					  settings: {
						slidesToShow: 3,
						slidesToScroll: 1,
						infinite: true,
						vertical: false,
						centerMode: false,
						centerPadding: '0px',
						dots: true,
						arrows: false
					  }
					},
					{
					  breakpoint: 767,
					  settings: {
						slidesToShow: 2,
						slidesToScroll: 1,
						infinite: true,
						vertical: false,
						centerMode: false,
						centerPadding: '0px',
						dots: true,
						arrows: false
					  }
					},
					{
					  breakpoint: 520,
					  settings: {
						slidesToShow: 1,
						slidesToScroll: 1,
						infinite: true,
						vertical: false,
						centerMode: false,
						centerPadding: '0px',
						dots: true,
						arrows: false
					  }
					}
				  ]
				});	
		
       });
  }
}


