import { Component, OnInit,Inject } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { JobService } from '../../../../services/job.service';
import { FormGroup,  FormBuilder,  Validators,FormControl } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { StripeScriptTag } from "stripe-angular";
import { StripeToken } from "stripe-angular";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {MatDialogModule} from '@angular/material/dialog';
import { Title } from '@angular/platform-browser';
import { StripeService, Elements, Element as StripeElement, ElementsOptions } from "ngx-stripe";

export interface DialogData {
  planId:any;type:any;
}

@Component({
  selector: 'app-edit',
  templateUrl: 'payment.component.html',
   styleUrls : [ './payment.component.css' ]
  
})
export class PaymentComponent implements OnInit {
	allplans:any="";user:any="";
	private publishableKey:string = "pk_test_oUX3CoG5KpQL5KxzgxwE6rQd";
	isLoading:boolean = true;
	
	
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute, private router: Router, private userservice: UserService,private jobservice: JobService,private fb: FormBuilder,public StripeScriptTag:StripeScriptTag,public dialog: MatDialog,private titleService: Title	) { 
		this.StripeScriptTag.setPublishableKey( this.publishableKey )
		this.getAllplans();
		this.titleService.setTitle( 'Payment | MoveLaunch' );
	}
	openDialog(planId): void {
		const dialogRef = this.dialog.open(StripePaymentDialog, {
		  width: '450px',
		  data: {planId:planId,type:'purchase'}
		});

		dialogRef.afterClosed().subscribe(result => {
			if(result == 'Success'){
				/* setTimeout(() => {           // or do some API calls/ Async events
					this.router.navigate(['/alljobs']);
				 }, 8500); */
				// this.ngOnInit();
				

				/* this.router.routeReuseStrategy.shouldReuseRoute = function(){
					return false;
				} */
				
			}
			
		});
	}
	 openDialogUpdate(planId): void {
		const dialogRef = this.dialog.open(StripePaymentDialog, {
		  width: '450px',
		  data: {planId:planId,type:'update'}
		});

		dialogRef.afterClosed().subscribe(result => {
			if(result == 'Success'){
				this.router.navigateByUrl('/profile', {skipLocationChange: true}).then(()=>	this.router.navigate(["/purchase-plans"])); 

				/* this.router.routeReuseStrategy.shouldReuseRoute = function(){
					return false;
				} */
				// this.ngOnInit();
			}
			
		});
	  }
	  
	  
	getAllplans(){
		this.jobservice.getAllPlans().subscribe(result =>
		{
			this.isLoading = true;
			if(result.success){
				this.isLoading = false;
				this.allplans = result.data;
				console.log(this.allplans);		
			}
		}); 
	} 
	isLoggedIn() {
	   let token = localStorage.getItem('login_token');
		if(token){
			return true ;
		}else{
			return false ;
		}
		
	}
		  
	ngOnInit() {
		this.router.navigate(['/purchase-plans']);
		var _user_id = localStorage.getItem('userid')
		this.userservice.getPartner(_user_id).subscribe(result => {
			this.user = result.user; 
		});	
	}
}

@Component({
    selector: 'dialog-overview-example-dialog',
	templateUrl: 'dialog-overview-example-dialog.html',
	styleUrls : [ './stripe-dialog.css' ]
})

export class StripePaymentDialog {
	public planId:any='';
	public type:any='';
	elements: Elements;
	card: StripeElement;
	stripeTest: FormGroup;
	isSubmit:boolean = false;
	elementsOptions: ElementsOptions = {
		locale: 'en',
		
	};
	  
  constructor(
  
    public dialogRef: MatDialogRef<StripePaymentDialog>,  @Inject(MAT_DIALOG_DATA) public data: DialogData,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder, private stripeService: StripeService,private _flashMessagesService: FlashMessagesService) {
		this.planId = data.planId;
		this.type = data.type;
		
		console.log(this.type);
	}
	
	
	ngOnInit() {
		this.stripeTest = this.fb.group({
		  plan_type: ['']
		});
		this.stripeService.elements(this.elementsOptions)
		  .subscribe(elements => {
			this.elements = elements;
			// Only mount the element the first time
			if (!this.card) {
			  this.card = this.elements.create('card', {
				hidePostalCode:true,
				style: {
				  base: {
					 iconColor: '#203767',
					color: '#616161',
					lineHeight: '40px',
					fontWeight: 500,
					fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
					fontSize: '18px',
				   ':-webkit-autofill': {
						color: '#fce883',
					},
					'::placeholder': {
						 color: '#87BBFD',
					},
				  }
				},
					
				
			  });
			  this.card.mount('#card-element');
			}
		  });
	}
  buy() {
	this.isSubmit = true;
	this.stripeService
      .createToken(this.card,{})
      .subscribe(result => {
		  console.log(result.token);
        if (result.token) {
			
			var _user_id = localStorage.getItem('userid')
			if(this.type == 'purchase'){
				this.jobservice.makePayment(result.token.id,this.planId,_user_id).subscribe(result =>
				{
					if(result.success){
						this.isSubmit = false;
						localStorage.setItem('total_bid',result.totalbids); 
						this._flashMessagesService.show("Plan purchased successfully", { cssClass: 'alert-success',timeout:6000});
							setTimeout(() => {           // or do some API calls/ Async events
							this.dialogRef.close('Success');
						},3000);
					}else{
						this._flashMessagesService.show(result.message.message, { cssClass: 'alert-error',timeout:6000});
					}
				   
				}); 
			}
			if(this.type == 'update'){
				this.jobservice.updatePayment(result.token.id,this.planId,_user_id).subscribe(result =>
				{
					if(result.success){
						this.isSubmit = false;
						localStorage.setItem('total_bid',result.totalbids); 
						
						this._flashMessagesService.show("Plan updated successfully", { cssClass: 'alert-success',timeout:6000});
						setTimeout(() => {           // or do some API calls/ Async events
							this.dialogRef.close('Success');
						},3000);
						 
						
					}else{
						this._flashMessagesService.show(result.message.message, { cssClass: 'alert-error',timeout:6000});
					}
				   
				}); 
			}
          
        } else if (result.error) {
         this.isSubmit = false;
          console.log(result.error.message);
        }
      }); 
  }
  

}


