import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { JobService } from './../../../../services/job.service';
import { FormGroup,  FormBuilder,  Validators,FormArray,FormControl } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from '../../../../../environments/environment';
import { Title } from '@angular/platform-browser';
class ImageSnippet {
	pending: boolean = false;
	status: string = 'init';
	
	constructor(public src: string, public file: File) {
		
	}
}

@Component({
  selector: 'app-edit',
  templateUrl: 'viewallnotifications.component.html',
  styleUrls : [ './viewallnotifications.component.css' ]
})

export class ViewAllNotificationsComponent implements OnInit {
	notificationCount:any = '';  notifications:any = '';jobservices:any = '';userselectedservices:any = '';
	
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private jobservice: JobService, private router: Router, private userservice: UserService, private fb: FormBuilder,private titleService: Title){ 
		this.titleService.setTitle( 'Notifications | MoveLaunch' );
	}
	getNotificationCount(){
		
		this.userservice.getNotificationCountForUser().subscribe(result => {
			this.notificationCount = result.count;
		});
		
	}

	getServices(){
		this.jobservice.getServices().subscribe(result => {
			this.jobservices =  result.data;
		}); 
	}
	
	getAllNotifications(){
		
		this.userservice.getAllNotifications(0).subscribe(result => {
			this.notifications = result.notfication;
			console.log(this.notifications);
			
				
			this.notifications.forEach((x,key) => {
				var setApp = [];
				for(var i=0;i<this.jobservices.length;i++ ){
					var servic = x.jobId.serviceId;
					var _id = this.jobservices[i]._id;
					var _name = this.jobservices[i].name;
					$.each(servic,function(index,value){
						
						if(value != "" && value == _id){
							 var item = _name;
							 setApp[i] = item;
						 } 
					 });  		
				}
				var filtered = setApp.filter(function (el) {
				  return el != null;
				});
					
			
				this.notifications[key].service_name = filtered.join(',');
				
				
			
			}); 
			
		});
		
	}
	


	ngOnInit() {
		this.getServices();
		this.getNotificationCount();
		this.getAllNotifications();
	}
}
