import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { JobService } from './../../../../services/job.service';
import { FormGroup,  FormBuilder,  Validators,FormArray,FormControl } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from '../../../../../environments/environment';
import { Title } from '@angular/platform-browser';
class ImageSnippet {
	pending: boolean = false;
	status: string = 'init';
	
	constructor(public src: string, public file: File) {
		
	}
}

@Component({
  selector: 'app-edit',
  templateUrl: 'editprofile.component.html',
  styleUrls : [ './editprofile.component.css' ]
})

export class EditProfileComponent implements OnInit {
	angForm: FormGroup;angPartnerForm: FormGroup;
	title = 'Login User';
	//error :any="";
	result :any ="";
	user:any =""; 
	states:any = '';
	counties:any = '';jobservices:any = '';setusertype:any = '';
	cities:any = '';
	submitted = false;
	submittedpartner = false;showthis = false;
	services:any='';
	stateName = '';
	statename = '';countyName = '';currentState='';currentCounty='';userid='';
	selectedFile:any='';interests = []; userImage = [];
	image_url ='';partner_image_url =''; 
	isChecked:any = '';userselectedservices:any="";
	
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private jobservice: JobService,
		 private router: Router, private userservice: UserService, private fb: FormBuilder,private titleService: Title, private changeDetectionRef:ChangeDetectorRef){ 
		this.LoginForm();
		this.addCheckboxes();
		this.titleService.setTitle( 'Edit Profile | MoveLaunch' );
		  
	}

	LoginForm() {
		this.angForm = this.fb.group({
			// name: ['',  Validators.required ],
			state: ['', Validators.required ],
			firstName: ['', Validators.required ],
			lastName: ['', Validators.required ],
			county: ['', Validators.required ],
			city: ['', Validators.required ],
			zip: ['', Validators.required ],
			 phone: ['', Validators.compose([Validators.required,Validators.pattern('^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$')])],
			address: ['', Validators.required ],
			imageInput: ['']
		  
		}); 
		this.angPartnerForm = this.fb.group({
			 // name: ['',  Validators.required ],
			  state: ['', Validators.required ],
			  firstName: ['', Validators.required ],
			  lastName: ['', Validators.required ],
			  companyName: ['', Validators.required ],
			  county: ['', Validators.required ],
			 // services: ['', Validators.required ],
			  services: new FormArray([]),
			  zip: ['', Validators.required ],
			  phone: ['', Validators.compose([Validators.required,Validators.pattern('^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$')])],
			  address: ['', Validators.required ],
			  imageInput: ['']
		}); 
		
	
	}
	private onSuccess() {
		this.selectedFile.pending = false;
		this.selectedFile.status = 'ok';
	}

	private onError() {
		this.selectedFile.pending = false;
		this.selectedFile.status = 'fail';
		this.selectedFile.src = '';
	}

	processFile(imageInput: any) {
		const file: File = imageInput.files[0];
		const reader = new FileReader();

		reader.addEventListener('load', (event: any) => {

		  this.selectedFile = new ImageSnippet(event.target.result, file);
		  this.selectedFile.pending = true;
		});
	
		reader.readAsDataURL(file);
		
	}
	

	getCounties(event){
		this.stateName = event.target.value;
		this.userservice.getCounties(this.stateName).subscribe(result => {
			this.counties = result;
		});
		
	}  
	getCities(event){
		this.countyName = event.target.value;
		
		this.statename = this.angForm.value.state;
		 this.userservice.getCities(this.countyName,this.statename).subscribe(result => {
			this.cities = result;
		}); 

	}  
	getServices(){
		this.jobservice.getServices().subscribe(result => {
			this.jobservices =  result.data;
		}); 
	}
	get f() { return this.angForm.controls; }
	get fpartner() { return this.angPartnerForm.controls; }
	onSubmit() {
        this.submitted = true;
		
        // stop here if form is invalid
        if(this.angForm.invalid){
			return;
        }else{
			var user = this.angForm.value;
			var _userid = localStorage.getItem('userid');
			
			if(this.selectedFile == undefined){
				this.userservice.updateUserWithoutFIle(user,_userid).subscribe(result =>
				{
					if(result.success){
						localStorage.setItem('firstname',user.firstName);
						localStorage.setItem('lastname',user.lastName);  
						this._flashMessagesService.show('Your Profile has been updated.', { cssClass: 'alert-success',timeout:2000}); 
						 setTimeout(() => {           // or do some API calls/ Async events
						  this.router.navigate(['/profile']);
						 }, 2200);
					}else{
						this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:5000});
					} 
				   
				});  
			}else{
				this.userservice.updateUser(user,_userid,this.selectedFile.file).subscribe(result =>
				{
					if(result.success){
						localStorage.setItem('firstname',user.firstName);
						localStorage.setItem('lastname',user.lastName);  
						this._flashMessagesService.show('Your Profile has been updated.', { cssClass: 'alert-success',timeout:2000}); 
						 setTimeout(() => {           // or do some API calls/ Async events
						  this.router.navigate(['/profile']);
						 }, 2200);
					}else{
						this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:5000});
					} 
				   
				}); 
			}
			 
			 
			
		}	
		
    }
	onSubmitPartner() {
		
        this.submittedpartner = true;
		
        // stop here if form is invalid
		
        if(this.angPartnerForm.invalid){
			
			 return;
        }else{
			
			const selectedOrderIds = this.angPartnerForm.value.services
			  .map((v, i) => v ? this.services[i]._id : null)
			  .filter(v => v !== null);
			
			this.angPartnerForm.value.services = selectedOrderIds;
			
			var user = this.angPartnerForm.value; 
			
			
			var _userid = localStorage.getItem('userid');
			
			if(this.selectedFile == undefined){
				
				this.userservice.updatePartnerWithoutFIle(user,_userid).subscribe(result =>
				{
					if(result.success){
						localStorage.setItem('firstname',user.firstName);
						localStorage.setItem('lastname',user.lastName);  
						//this._flashMessagesService.show('Your Profile has been updated.', { cssClass: 'alert-success',timeout:5000}); 
						 setTimeout(() => {           // or do some API calls/ Async events
						  this.router.navigate(['/profile']);
						 }, 2200);
					}else{
						this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:2000});
					} 
				   
				});  
			}else{
				this.userservice.updatePartner(user,_userid,this.selectedFile.file).subscribe(result =>
				{
					if(result.success){
						
						localStorage.setItem('firstname',user.firstName);
						localStorage.setItem('lastname',user.lastName);  
						this._flashMessagesService.show('Your Profile has been updated.', { cssClass: 'alert-success',timeout:2000}); 
						 setTimeout(() => {           // or do some API calls/ Async events
						  this.router.navigate(['/profile']);
						 }, 2200);
						
					}else{
						this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:5000});
					} 
				   
				}); 
			} 
			 
			 
			
		}	
		
    }
	private addCheckboxes() {
		this.jobservice.getServices().subscribe(result => {
			this.services = result.data;
			 this.services.map((o, i) => {
			  const control = new FormControl(i === 0); // if first item set to true, else false
			  (this.angPartnerForm.controls.services as FormArray).push(control);
			});
		}); 
	}
	  
	onCheckboxChagen(event, value) {
		
		if (event.checked) {

		  this.interests.push(value);
		} 

		if (!event.checked) {
			
			let index = this.interests.indexOf(value);
			if (index > -1) {
				this.interests.splice(index, 1);
			}
		}
		
		
	}
	ngOnDestroy(){
      this.changeDetectionRef.detach();
    }
	
	ngOnInit() {
		
		
		
		this.setusertype =  localStorage.getItem('user_role');
		this.isChecked = true;
		
		if(this.setusertype == 'user'){
			this.userservice.getUsers().subscribe(result => {
				console.log('i got it ', result);

				this.user = result[0]; 
				var currentState = this.user.state;
				var currentCounty = this.user.county;
				var user_pic = this.user.profile_pic;
				var user_id = this.user._id;
				
				this.userservice.getStates().subscribe(result => {
					this.states = result;
				});

				if(currentState != undefined || currentState != 'undefined') {
					this.userservice.getCounties(currentState).subscribe(result => {
						this.counties = result;
					}); 
				} else {
					this.userservice.getCounties(this.states).subscribe(result => {
						this.counties = result;
					}); 
				}
				
				if(currentCounty != undefined || currentCounty != 'undefined') {
					this.userservice.getCities(currentCounty,currentState).subscribe(result => {
						this.cities = result; 
					});
				} else {
					this.userservice.getCities(this.counties, this.states).subscribe(result => {
						this.counties = result;
					}); 
				}
				
				 
				this.userImage['src'] = environment.image_url+user_id+'/'+user_pic;
				this.selectedFile = this.userImage;
				return this.user;
			});
		}
		
		if(this.setusertype == 'partner'){
			var _user_id = localStorage.getItem('userid')
			this.userservice.getPartner(_user_id).subscribe(result => {
				
				
				this.user = result.user; 
				
				var setApp = [];
				for(var i=0;i<this.services.length;i++ ){
					var servic = this.user.services;
					var _id = this.services[i]._id;
					$.each(servic,function(index,value){
						 if(value == _id){
							 var item = value;
							 setApp[i] = item;
						 }
					 });  		
				}	
				if(this.user.customer_id){
					this.showthis = false;
				}else{
					this.showthis = true;
				}
				var currentState = this.user.state;
				var currentCounty = this.user.county;
				var user_pic = this.user.profile_pic;
				var user_id = this.user._id;
				
				this.userservice.getStates().subscribe(result => {
					this.states = result;
				});
				
				if(currentState != undefined || currentState != 'undefined') {
					this.userservice.getCounties(currentState).subscribe(result => {
						this.counties = result;
					}); 
				} else {
					this.userservice.getCounties(this.states).subscribe(result => {
						this.counties = result;
					}); 
				}
				
				if(currentCounty != undefined || currentCounty != 'undefined') {
					this.userservice.getCities(currentCounty,currentState).subscribe(result => {
						this.cities = result; 
					});
				} else {
					this.userservice.getCities(this.counties, this.states).subscribe(result => {
						this.counties = result;
					}); 
				}

				this.userselectedservices = setApp;

				if(user_pic){
					this.userImage['src'] = environment.partner_image_url+user_id+'/'+user_pic;
					this.selectedFile = this.userImage;
				}
				
				
				return this.user;
			});
		} 
		
		if (!this.changeDetectionRef['destroyed']) {
			this.changeDetectionRef.detectChanges();
		}

		this.getServices();
	}
}
