@Component({
  selector: 'app-edit',
  templateUrl: 'dashboard.component.html',
  styleUrls : [ './dashboard.component.css' ]
})

export class DashboardComponent implements OnInit {
		first_show = false;val:any='';userslist:any='';compuserslist:any='';bidsData:any='';
	allJobs:any="";allCompletedJobs:any="";jobservices:any="";allservice:any="";
	isValid : boolean = false;
	name:any='';animal:any='';jobidisset:any='';
	message:any='';
	show_dialog : boolean = false;
	button_text:any='';image_url:any='';hire_decline:any='';decline_hire:any='';
	constructor(private _flashMessagesService: FlashMessagesService,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder,public dialog: MatDialog,private titleService: Title,private route: ActivatedRoute,private router: Router)
	{
		this.getAllJobs();
		setTimeout(() => {  
			this.getAllCompletedJobs();
		},5200);
		
		this.titleService.setTitle( 'Dashboard | MoveLaunch' );
	}
	openDialog(type,jobID,userid,Int_job_id): void {
		const dialogRef = this.dialog.open(HireDeclineDialogDashboard, {
		  width: '450px',
		  data: {jobID:jobID,type:type,userid:userid,Int_job_id:Int_job_id}
		});

		dialogRef.afterClosed().subscribe(result => {
			
			console.log(result);
			if(result == 'Hired'){
				this._flashMessagesService.show('Estimates has been hired.', { cssClass: 'alert-success',timeout:2000});
				this.hire_decline = this.hire_decline ? 'Hired' : 'Hire';
				this.decline_hire = this.decline_hire ? 'Decline' : 'Declined';
				
				setTimeout(() => {           // or do some API calls/ Async events
					this.router.navigate(['/user-in-progress-jobs']);
				},2200);
			}
			if(result == 'Declined'){
				this._flashMessagesService.show('Estimates has been Declined.', { cssClass: 'alert-success',timeout:2000});
				this.decline_hire = this.decline_hire ? 'Declined' : 'Decline';
				this.hire_decline = this.decline_hire ? 'Hire' : 'Hired';
			}
			
		});
	}
	
	getAllJobs(){
		this.jobservice.getAllJobsWithBids().subscribe(result =>
		{
			this.isValid = true;
			if(result.success){
				this.isValid = true;
				var d = new Date();
				var month = '' + (d.getMonth() + 1);
				var day = '' + d.getDate();
				var year = d.getFullYear();
				if (month.length < 2) month = '0' + month;
				if (day.length < 2) day = '0' + day;
				var mindateset = [year, month, day].join('-');
				
				this.allJobs = result.job;
				console.log(this.allJobs);
				let userslist1 = [];
				this.allJobs.forEach((x,key) => {
					x.bidsData.forEach((value,index)=>{
						userslist1.push({'job_id':x._id,'user_id':value.userId._id,'bid_price':value.bid_price,'attachment_pic':value.attachment_pic,'created_at':value.created_at,'bidStatus':value.bidStatus,'companyName':value.userId.companyName,'jobId':x.jobId,'job_created':x.created_at,'width':value.rating*20});		
					});
				}); 
				userslist1.sort((a,b) => b.created_at.localeCompare(a.created_at));
				this.userslist = userslist1;
				console.log(this.userslist);
			}else{
				this.isValid = true;
				this._flashMessagesService.show("No Estimates Found.", { cssClass: 'alert-danger',timeout:6000});
			} 
		   
		}); 
	}
	

	getAllCompletedJobs(){
		
		this.jobservice.getFilteredJobsForUserDashboard(2).subscribe(result =>
		{
			if(result.success){
				this.isValid = true;
				var d = new Date();
				var month = '' + (d.getMonth() + 1);
				var day = '' + d.getDate();
				var year = d.getFullYear();
				if (month.length < 2) month = '0' + month;
				if (day.length < 2) day = '0' + day;
				var mindateset = [year, month, day].join('-');
				//console.log(result.jobs);
				this.allCompletedJobs = result.jobs;
				let userslist2 = [];
				this.allCompletedJobs.forEach((x,key) => {
					x.bidsData.forEach((value,index)=>{
						userslist2.push({'job_id':x._id,'user_id':value.userId._id,'bid_price':value.bid_price,'attachment_pic':x.attachment_pic,'created_at':value.created_at,'bidStatus':value.bidStatus,'companyName':value.userId.companyName,'jobId':x.jobId,'job_created':x.created_at,'width':value.rating*20});		
					});
				}); 
				userslist2.sort((a,b) => b.created_at.localeCompare(a.created_at));
				this.compuserslist = userslist2;
				console.log(this.compuserslist);
			}else{
				this.isValid = true;
				//this._flashMessagesService.show("No job founds", { cssClass: 'alert-danger',timeout:6000});
			}   
		   
		});
			
	}
	
	ngOnInit() {
		this.first_show = true;
		this.isValid = false;
		
		this.hire_decline = 'Hire';
		this.decline_hire = 'Decline';
	}
}
@Component({
  selector: 'dialog-hire-decline',
  templateUrl: 'dialog-hire-decline.html',
  styleUrls : [ './dialog-hire-decline.css' ]
})
export class HireDeclineDialogDashboard {
	
	public jobID:any='';
 	public type:any='';
 	public userid:any='';
	public Int_job_id:any='';
 
  constructor(
    public dialogRef: MatDialogRef<HireDeclineDialogDashboard>,  @Inject(MAT_DIALOG_DATA) public data: DialogData,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder) {
		
		console.log(data);
		this.jobID = data.jobID;
		this.type = data.type;
		this.userid = data.userid;
		this.Int_job_id = data.Int_job_id;
	} 

	onNoClick(): void {
		this.dialogRef.close('ignore');
	}
	onYesClick(type_sent): void {
		console.log(this.jobID);
		console.log(this.userid);
		console.log(this.Int_job_id);
		
			
		this.jobservice.hireDecline(type_sent,this.jobID,this.userid,).subscribe(result =>
		{
			
			if(result.success && type_sent ==1){
				console.log('myclass_'+this.Int_job_id);
				var myElement = document.getElementsByClassName('myclass_'+this.Int_job_id) as HTMLCollectionOf<HTMLElement>
				for (var i = 0; i < myElement.length; i++){
					myElement[i].style.display = "none";
				}
				
				this.dialogRef.close('Hired');
			}
			if(result.success && type_sent ==2){
				var myElement = document.getElementsByClassName('myclass_'+this.Int_job_id) as HTMLCollectionOf<HTMLElement>
				for (var i = 0; i < myElement.length; i++){
					myElement[i].style.display = "none";
				}
		
				this.dialogRef.close('Declined');
			} 
			
			
		});   
		
	}

}