import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { FormGroup,  FormBuilder,  Validators,FormControl } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-edit',
  templateUrl: 'resetpassword.component.html'
})
export class ResetpasswordComponent implements OnInit {
  angForm: FormGroup;
  title = 'Login User';
  //error :any="";
  result :any ="";  
   submitted = false;
  verifycode ='';
  
  constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute, private router: Router, private userservice: UserService, private fb: FormBuilder,private titleService: Title	) { 
    this.LoginForm();
	this.titleService.setTitle( 'Reset Password | MoveLaunch' );
   }
	
  LoginForm() {
    this.angForm = this.fb.group({ 
    password:['',  Validators.compose([Validators.required, Validators.minLength(5), Validators.maxLength(10)])],
   });
  }
 get f() { return this.angForm.controls; }
    onSubmit() {
        this.submitted = true;
	
        // stop here if form is invalid
        if(this.angForm.invalid){
			 return;
        }else{
			var user = this.angForm.value;
			this.verifycode = this.route.snapshot.paramMap.get('verifycode');
			console.log(user.password);
			this.userservice.resetPassword(user.password,this.verifycode).subscribe(result =>
			{
				
				 if(result.success){
					this._flashMessagesService.show(result.message, { cssClass: 'alert-success',timeout:5000});
					this.router.navigate(['/login']);
				  }else{
					  this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:10000}); 
				  
				  }  
			   
			}); 
			 
			
		}	
		
    }
	

  ngOnInit() {
	this.verifycode = this.route.snapshot.paramMap.get('verifycode');
	this.userservice.password_code(this.verifycode).subscribe(result => {
		if(!result.success){
			this._flashMessagesService.show("This code has been expired.", { cssClass: 'alert-danger',timeout:2000});
		} 	
		
	});

  }
}


