import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { JobService } from './../../../../services/job.service';
import { FormGroup,  FormBuilder,  Validators,FormArray,FormControl } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from '../../../../../environments/environment';
import { Title } from '@angular/platform-browser';
class ImageSnippet {
	pending: boolean = false;
	status: string = 'init';
	
	constructor(public src: string, public file: File) {
		
	}
}

@Component({
  selector: 'app-edit',
  templateUrl: 'profile.component.html',
  styleUrls : [ './profile.component.css' ]
})

export class ProfileComponent implements OnInit {
	angForm: FormGroup;angPartnerForm: FormGroup;
	title = 'Login User';
	//error :any="";
	result :any ="";
	user:any =""; 
	states:any = '';
	counties:any = '';jobservices:any = '';setusertype:any = '';
	cities:any = '';
	submitted = false;
	submittedpartner = false;showthis = false;
	services:any='';
	stateName = '';
	statename = '';countyName = '';currentState='';currentCounty='';userid='';
	selectedFile:any='';interests = []; userImage = [];
	image_url ='';partner_image_url =''; 
	isChecked:any = '';userselectedservices:any="";selectedState:any="";
	
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private jobservice: JobService,
		 private router: Router, private userservice: UserService, private fb: FormBuilder,private titleService: Title, private changeDetectionRef:ChangeDetectorRef){ 
		
		//this.addCheckboxes();
		this.titleService.setTitle( 'Profile | MoveLaunch' );
		  
	}


	ngOnDestroy(){
      this.changeDetectionRef.detach();
    }

	getCounties(event){
		this.stateName = event.target.value;
		this.userservice.getCounties(this.stateName).subscribe(result => {
			this.counties = result;
		});
		
	}  
	getCities(event){
		this.countyName = event.target.value;
		
		this.statename = this.angForm.value.state;
		 this.userservice.getCities(this.countyName,this.statename).subscribe(result => {
			this.cities = result;
		}); 

	}  
	getServices(){
		this.jobservice.getServices().subscribe(result => {
			this.jobservices =  result.data;
		}); 
	}
	
	ngOnInit() {
		if (!this.changeDetectionRef['destroyed']) {
			this.changeDetectionRef.detectChanges();
		}


		this.setusertype =  localStorage.getItem('user_role');
		this.isChecked = true;
		
		if(this.setusertype == 'user'){
			this.userservice.getUsers().subscribe(result => {
				console.log('i got it ', result);

				this.user = result[0]; 
				var currentState = this.user.state;
				var currentCounty = this.user.county;
				var user_pic = this.user.profile_pic;
				var user_id = this.user._id;
				
				this.userservice.getStates().subscribe(result => {
					this.states = result;
					//console.log(this.states);
					this.states.forEach((x,key) => {
						if(currentState == x.abbv){
							console.log(x);
							this.selectedState = x.name;
						}
					});
				});

				if(currentState != undefined || currentState != 'undefined') {
					this.userservice.getCounties(currentState).subscribe(result => {
						this.counties = result;
					}); 
				} else {
					this.userservice.getCounties(this.states).subscribe(result => {
						this.counties = result;
					}); 
				}
				
				if(currentCounty != undefined || currentCounty != 'undefined') {
					this.userservice.getCities(currentCounty,currentState).subscribe(result => {
						this.cities = result; 
					});
				} else {
					this.userservice.getCities(this.counties, this.states).subscribe(result => {
						this.counties = result;
					}); 
				}
				
				 
				this.userImage['src'] = environment.image_url+user_id+'/'+user_pic;
				this.selectedFile = this.userImage;
				return this.user;
			});
		}
		
		if(this.setusertype == 'partner'){
			var _user_id = localStorage.getItem('userid')
			this.userservice.getPartner(_user_id).subscribe(result => {
				
				
				this.user = result.user; 
				console.log(this.jobservices);
				var setApp = [];
				for(var i=0;i<this.jobservices.length;i++ ){
					var servic = this.user.services;
					var _id = this.jobservices[i]._id;
					var _name = this.jobservices[i].name;
					$.each(servic,function(index,value){
						 if(value == _id){
							 var item = _name;
							 setApp[i] = item;
						 }
					 });  		
				}	
				if(this.user.customer_id){
					this.showthis = false;
				}else{
					this.showthis = true;
				}
				var currentState = this.user.state;
				var currentCounty = this.user.county;
				var user_pic = this.user.profile_pic;
				var user_id = this.user._id;
				
				this.userservice.getStates().subscribe(result => {
					this.states = result;
					this.states.forEach((x,key) => {
						if(currentState == x.abbv){
							console.log(x);
							this.selectedState = x.name;
						}
					});
				});
				
				if(currentState != undefined || currentState != 'undefined') {
					this.userservice.getCounties(currentState).subscribe(result => {
						this.counties = result;
					}); 
				} else {
					this.userservice.getCounties(this.states).subscribe(result => {
						this.counties = result;
					}); 
				}
				
				if(currentCounty != undefined || currentCounty != 'undefined') {
					this.userservice.getCities(currentCounty,currentState).subscribe(result => {
						this.cities = result; 
					});
				} else {
					this.userservice.getCities(this.counties, this.states).subscribe(result => {
						this.counties = result;
					}); 
				}

				this.userselectedservices = setApp;
				console.log(this.userselectedservices);
				if(user_pic){
					this.userImage['src'] = environment.partner_image_url+user_id+'/'+user_pic;
					this.selectedFile = this.userImage;
				}
				
				
				return this.user;
			});
		} 
		
		
		this.getServices();
	}
}
