import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { FormGroup,  FormBuilder,  Validators,FormControl,FormGroupDirective,NgForm } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ErrorStateMatcher } from '@angular/material/core';
import { Title } from '@angular/platform-browser';
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const invalidCtrl = !!(control && control.invalid && control.parent.dirty);
    const invalidParent = !!(control && control.parent && control.parent.invalid && control.parent.dirty);

    return (invalidCtrl || invalidParent);
  }
}

@Component({
  selector: 'app-edit',
  templateUrl: 'changepassword.component.html'
})
export class ChangepasswordComponent implements OnInit {
  angForm: FormGroup;
  title = 'Login User';
  //error :any="";
  result :any ="";
   submitted = false;
  verifycode ='';
  matcher = new MyErrorStateMatcher();
  
  constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute, private router: Router, private userservice: UserService, private fb: FormBuilder,private titleService: Title	) { 
    this.LoginForm();
			this.titleService.setTitle( 'Change Passowrd | MoveLaunch' );
   }
	
  LoginForm() {
    this.angForm = this.fb.group({ 
		password:['',  Validators.compose([Validators.required, Validators.minLength(5), Validators.maxLength(10)])],
		confirm_password:['',  Validators.compose([Validators.required, Validators.minLength(5), Validators.maxLength(10)])],
	}, {validator: this.checkPasswords });

	
  }
	get f() { 
	
		return this.angForm.controls; 
	}
    onSubmit() {
		
        this.submitted = true;
	
        // stop here if form is invalid
        if(this.angForm.invalid){
			 return;
        }else{
			var user = this.angForm.value;
			//this.verifycode = this.route.snapshot.paramMap.get('verifycode');
			
			this.userservice.updatePassword(user.password).subscribe(result =>
			{
				
				if(result.success){
					this._flashMessagesService.show(result.message, { cssClass: 'alert-success',timeout:5000});
				}else{
					  this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:10000}); 
				}  
			   
			}); 
			 
			
		}	
		
    }
	checkPasswords(group: FormGroup) { // here we have the 'passwords' group
		let pass = group.controls.password.value;
		let confirmPass = group.controls.confirm_password.value;
		return pass === confirmPass ? null : { notSame: true }     
	}

  ngOnInit() {
	/* this.verifycode = this.route.snapshot.paramMap.get('verifycode');
	this.userservice.password_code(this.verifycode).subscribe(result => {
		if(!result.success){
			this._flashMessagesService.show("This code has been expired.", { cssClass: 'alert-danger',timeout:2000});
		} 	
		
	}); */

  }
}


