import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../../services/user.service';
import { JobService } from './../../../../services/job.service';
import { FormGroup,  FormBuilder,  Validators,FormArray,FormControl } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from '../../../../../environments/environment';
import { Title } from '@angular/platform-browser';
class ImageSnippet {
	pending: boolean = false;
	status: string = 'init';
	
	constructor(public src: string, public file: File) {
		
	}
}

@Component({
  selector: 'app-edit',
  templateUrl: 'viewpartnerprofile.component.html',
  styleUrls : [ './viewpartnerprofile.component.css' ]
})

export class PartnerProfileComponent implements OnInit {
	angForm: FormGroup;angPartnerForm: FormGroup;
	title = 'Login User';
	//error :any="";
	result :any ="";
	user:any =""; 
	partnerprofiledata:any =""; reviewData:any =""; 
	states:any = '';
	counties:any = '';jobservices:any = '';setusertype:any = '';
	cities:any = '';
	submitted = false;
	submittedpartner = false;showthis = false;
	services:any='';
	stateName = '';
	statename = '';countyName = '';currentState='';currentCounty='';userid='';
	selectedFile:any='';interests = []; userImage = [];
	image_url ='';partner_image_url =''; 
	isChecked:any = '';userselectedservices:any="";
	
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private jobservice: JobService, private router: Router, private userservice: UserService, private fb: FormBuilder,private titleService: Title){ 
		this.getServices();
		this.titleService.setTitle( 'Profile | MoveLaunch' );
	}
	getServices(){
		this.jobservice.getServices().subscribe(result => {
			this.jobservices =  result.data;
			
		}); 
	}
	ngOnInit() {
		
			var _user_id = this.route.snapshot.paramMap.get('id');
			this.userservice.getPartner(_user_id).subscribe(result => {
				
				
				this.user = result.user; 
				
				var setApp = [];
				for(var i=0;i<this.services.length;i++ ){
					var servic = this.user.services;
					var _id = this.services[i]._id;
					$.each(servic,function(index,value){
						 if(value == _id){
							 var item = value;
							 setApp[i] = item;
						 }
					 });  		
				}	
				if(this.user.customer_id){
					this.showthis = false;
				}else{
					this.showthis = true;
				}
				var currentState = this.user.state;
				var currentCounty = this.user.county;
				var user_pic = this.user.profile_pic;
				var user_id = this.user._id;
				
				this.userservice.getStates().subscribe(result => {
					this.states = result;
				});
				
				this.userservice.getCounties(currentState).subscribe(result => {
					this.counties = result;
				}); 
				this.userservice.getCities(currentCounty,currentState).subscribe(result => {
					this.cities = result; 
				});
				this.userselectedservices = setApp;
				if(user_pic){
					this.userImage['src'] = environment.partner_image_url+user_id+'/'+user_pic;
					this.selectedFile = this.userImage;
				}
				return this.user;
			});
			this.userservice.getPartnerJobProfile(_user_id).subscribe(result => {
				
				this.partnerprofiledata = result.data;
				this.partnerprofiledata.totalJobs = result.data.Totalcompleted + result.data.Totalprocessing;
				this.partnerprofiledata.width = result.data.Totalrating*20;
				
			});
			
			this.userservice.getPartnerCompletedJobsAndReviews(_user_id).subscribe(result => {
				console.log(result);
				this.reviewData = result.data;
			});
		
		
		
	}
}
